.git {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 4rem 2rem;
}
.github_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  width: 100% !important;
}
.github_container > div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  padding: 2rem;
}
.github_container a {
  width: 100%;
  height: auto;
}
.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95%;
  gap: 2rem;
}
.github_container img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.react-activity-calendar__footer {
  font-size: 3rem;
}

@media screen and (max-width: 1024px) {
  .github_container {
    width: 100% !important;
  }
  .github_container > div {
    width: 100% !important;
  }
  .flex {
    flex-direction: column;
    width: 100% !important;
    gap: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .github_container {
    width: 100% !important;
  }
  .github_container > div {
    width: 100% !important;
  }
  .flex {
    flex-direction: column;
    width: 100% !important;
    gap: 2rem;
  }
  .github_container footer {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.7rem !important;
  }
}
